.header{
  position: relative;
  width: 1920px;
  height: 139px;
  .header_left{
    display: inline-block;
    width: 630px;
    height: 139px;
    ul{
      
      width: 100%;
      height: 100%;
      li{
        display: inline-block;
        margin:  61px 0 0 20px;
        width: 127px;
        height: 43px;
        font-size: 16px;
        letter-spacing: 2px;
        color: #fff;
        text-align: center;
        line-height: 43px;
        span{
          position: relative;
          display: inline-block;
          width: 20px;
          height: 20px;
          top: 4px;
          left: -5px;
        }
      }
    }
  }

  .logo{
    position: absolute;
    top: 7px;
    left: 286px;
    width: 1351px;
    height: 71px;
    .title{
      position: absolute;
      width: 387px;
      height: 50px;
      top: 16px;
      left: 480px;
    }
  }
  .weather{
    position: absolute;
    top: 43px;
    left: 1274px;
    display:inline-block; 
    width: 253px;
    height: 76px;
    .left{
      float: left;
      width: 39px;
      height: 31px;
      margin: 24px 0 0 12px;
    }
    .center{
      float: left;
      width: 24%;
      
      color:#fff45c;
      margin: 15px 0 0 5px;
      p{
        font-size: 18px;
        text-align: center;
        margin-bottom: 0px;
        i{
          font-style: normal;
          font-size: 12px;
        }
      }
      span{
        display: inline-block;
        font-size: 12px;
      }
      }
      .wire{
        display: inline-block;
        margin-top: 24px;
        float: left;
        width: 1px;
        height: 32px;
        background-color: #fff;
    }
    .time{
      margin: 13px 0 0 8px;
      float: left;
      width: 120px;
      height: 40px;
      p{
        font-size: 22px;
        margin-bottom: 0px;
        letter-spacing: 1px;
        color: #ffffff;
        text-shadow: 0 0 30px rgb(244, 250, 245);
      }
      span{
        font-size: 12px;
        color: #ffffff;
        text-shadow: 0 0 30px rgb(244, 250, 245);
      }
    }
  }
  .future_weather{
    position: absolute;
    top: 35px;
    left: 1562px;
    width: 300px;
    height: 90px;
    ul{
      margin: 0;
      padding: 0;
      display: inline-block;
      width: 20%;
      height: 100%;
      li{
        width: 100%;
        text-align: center;
        list-style: none;
      }
      li:nth-child(1){
       font-size: 12px;
       color: #ffffff;
      }
      li:nth-child(2){
        font-size: 12px;
        color: #0090ff;
       }
       li:nth-child(3){
        margin-left: 15px;
        display: inline-block;
        width: 25px;
        height: 24px;
       }
       li:nth-child(4){
        font-size: 12px;
        color: #fff;
       }
       li:nth-child(5){
        font-size: 12px;
        color: #fff;
       }
    }
  }
}
.major{
  position: absolute;
  top: 78px;
  left: 687px;
  width: 547px;
  height: 47px;
  font-size: 16px;
  color: #fff;


  .imgbox{
    float: left;
    display: inline-block;
    width: 20px;
    height: 20px;
    margin: 17px 14px 0 44px;
  
  }
  .text{
    margin-top: 14px;
    float: left;
    display: inline-block;
    width: 455px;
    height: 46px;
    font-size: 16px;
    color: #fff;
  }
}