.header {
  position: relative;
  width: 1920px;
  height: 139px;
}
.header .header_left {
  display: inline-block;
  width: 630px;
  height: 139px;
}
.header .header_left ul {
  width: 100%;
  height: 100%;
}
.header .header_left ul li {
  display: inline-block;
  margin: 61px 0 0 20px;
  width: 127px;
  height: 43px;
  font-size: 16px;
  letter-spacing: 2px;
  color: #fff;
  text-align: center;
  line-height: 43px;
}
.header .header_left ul li span {
  position: relative;
  display: inline-block;
  width: 20px;
  height: 20px;
  top: 4px;
  left: -5px;
}
.header .logo {
  position: absolute;
  top: 7px;
  left: 286px;
  width: 1351px;
  height: 71px;
}
.header .logo .title {
  position: absolute;
  width: 387px;
  height: 50px;
  top: 16px;
  left: 480px;
}
.header .weather {
  position: absolute;
  top: 43px;
  left: 1274px;
  display: inline-block;
  width: 253px;
  height: 76px;
}
.header .weather .left {
  float: left;
  width: 39px;
  height: 31px;
  margin: 24px 0 0 12px;
}
.header .weather .center {
  float: left;
  width: 24%;
  color: #fff45c;
  margin: 15px 0 0 5px;
}
.header .weather .center p {
  font-size: 18px;
  text-align: center;
  margin-bottom: 0px;
}
.header .weather .center p i {
  font-style: normal;
  font-size: 12px;
}
.header .weather .center span {
  display: inline-block;
  font-size: 12px;
}
.header .weather .wire {
  display: inline-block;
  margin-top: 24px;
  float: left;
  width: 1px;
  height: 32px;
  background-color: #fff;
}
.header .weather .time {
  margin: 13px 0 0 8px;
  float: left;
  width: 120px;
  height: 40px;
}
.header .weather .time p {
  font-size: 22px;
  margin-bottom: 0px;
  letter-spacing: 1px;
  color: #ffffff;
  text-shadow: 0 0 30px #f4faf5;
}
.header .weather .time span {
  font-size: 12px;
  color: #ffffff;
  text-shadow: 0 0 30px #f4faf5;
}
.header .future_weather {
  position: absolute;
  top: 35px;
  left: 1562px;
  width: 300px;
  height: 90px;
}
.header .future_weather ul {
  margin: 0;
  padding: 0;
  display: inline-block;
  width: 20%;
  height: 100%;
}
.header .future_weather ul li {
  width: 100%;
  text-align: center;
  list-style: none;
}
.header .future_weather ul li:nth-child(1) {
  font-size: 12px;
  color: #ffffff;
}
.header .future_weather ul li:nth-child(2) {
  font-size: 12px;
  color: #0090ff;
}
.header .future_weather ul li:nth-child(3) {
  margin-left: 15px;
  display: inline-block;
  width: 25px;
  height: 24px;
}
.header .future_weather ul li:nth-child(4) {
  font-size: 12px;
  color: #fff;
}
.header .future_weather ul li:nth-child(5) {
  font-size: 12px;
  color: #fff;
}
.major {
  position: absolute;
  top: 78px;
  left: 687px;
  width: 547px;
  height: 47px;
  font-size: 16px;
  color: #fff;
}
.major .imgbox {
  float: left;
  display: inline-block;
  width: 20px;
  height: 20px;
  margin: 17px 14px 0 44px;
}
.major .text {
  margin-top: 14px;
  float: left;
  display: inline-block;
  width: 455px;
  height: 46px;
  font-size: 16px;
  color: #fff;
}
